/* General Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, sans-serif;
}

body {
    background-color: #f9f9f9;
}

/* Dashboard Container */
.dashboard-container {
    margin: auto;
    padding: 2rem;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

/* Two-column Layout */
.content-layout {
    display: flex;
    gap: 2rem;
}

.left-column {
    flex: 1.5;
}

.right-column {
    flex: 1;
    background-color: #f8f8f8;
    padding: 1.5rem;
    border-left: 1px solid #ddd;
    border-radius: 6px;
}

/* Header Section */
.header h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.header span {
    font-weight: bold;
}

.stats p {
    margin: 0.5rem 0;
    font-size: 1rem;
}

/* Section List */

.section-list {
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.section-item {
    display: flex;
    align-items: center;
    padding: 1rem;
    border: 1px solid #ddd;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s ease;
}

.section-item.selected {
    border-color: #007bff;
    background-color: #e7f1ff;
}

.section-details {
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-size: 0.9rem;
}

.section-name {
    font-weight: bold;
}

/* Instructions Section */
.right-column h2 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.instructions-list {
    list-style: decimal inside;
    font-size: 0.95rem;
    color: #555;
    margin-left: 1rem;
    margin-bottom: 1.5rem;
}

.instructions-list li {
    margin: 0.5rem 0;
}

.instructions-actions {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.instructions-actions a {
    text-decoration: none;
    color: #007bff;
    font-weight: bold;
}

.instructions-actions a:hover {
    text-decoration: underline;
}